* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
   list-style: none;
   margin: 20px 20px;
}

.title {
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 50px;
}
.text {
    font-size: 20px;
    font-weight: 500;
}
.text-title {
    font-size: 24px;
    font-weight: 700;
}
